@import "base/variables";
@import "mixins/importMixins";
@import "base/font";

@include meyer-reset();

.bg-no-repeat{
  background: no-repeat 0 0;
}

.logo-with-text {
  @extend .bg-no-repeat;
  @media (max-width: 1024px) {
    display: none !important;
  }
  width: 231px;
  height: 55px;
  background-image: url('data:image/png;base64,');
}
.logo {
  @extend .bg-no-repeat;
  width: 48px;
  height: 48px;
  display: none !important;
  @media (max-width: 1024px) {
    display: inline-block !important;
  }
  background-image: url('data:image/png;base64,');
}
.header{
  position: fixed;
  height: 56px;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: $prime-green;
  >[class^="icon-"], >[class*=" icon-"]{
    font-size: ceil($font-size-large*2.2);
    color: $toolbar-tool;
    border-radius: ceil($font-size-large*2.2);
    @include css3("transition",all 0.3s);
    &:hover{
      background-color: $toolbar-set;
    }
  }
  >span{
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
  }
  @media (max-width: 1024px) {
    height: 48px;
  }
}
.footer{
  @extend .header;
  background-color: transparent;
  position: fixed;
  top: 15px;
  bottom: auto;
  left: auto;
  right: 15px;
  >[class^="icon-"], >[class*=" icon-"]{
    font-size: ceil($font-size-large*1.8);
    margin-left: 5px;
  }
  @media (max-width: 1024px) {
    height: 37px;
    background-color: $gray-98;
    @include border("border-top");
    padding: 5px;
    top: auto;
    bottom: 0;
    left: 0;
    right: 0;
    height: auto;
    >[class^="icon-"], >[class*=" icon-"]{
      color: $toolbar-set;
      &:hover{
        color: $gray-100;
      }
    }
  }
}

.torrent-list{
  position: fixed;
  top: 56px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  >li{
    list-style: none;
    @include yahei();
    padding: 10px;
    @include border("border-bottom");
    &:last-child{
      border: none;
    }
  }
  @media (max-width: 1024px) {
    top: 48px;
    bottom: 37px;
  }
}